import { createApi, fetchBaseQuery } from "@reduxjs/toolkit/query/react"

export const farmerApi = createApi({
  reducerPath: "farmerApi",
  baseQuery: fetchBaseQuery({
    baseUrl: "http://localhost:8080/api", // change to prod URL later
  }),
  endpoints: (builder) => ({
    createFarmerProfile: builder.mutation<any, any>({
      query: (body) => ({
        url: "/user",
        method: "POST",
        body,
      }),
    }),
  }),
})

export const { useCreateFarmerProfileMutation } = farmerApi
